"use client";

import React from "react";
import Link from "next/link";

const Footer = () => {
  return (
    <footer className="border-t border-white/5 py-12 px-6 bg-black/30 backdrop-blur-sm">
      <div className="max-w-7xl mx-auto flex flex-col md:flex-row justify-between items-center gap-8">
        <div className="flex flex-col items-center md:items-start">
          <Link href="/" className="text-2xl font-bold tracking-tighter text-white mb-2">
            BWT<span className="text-brand-blue">.</span>
          </Link>
          <p className="text-white/30 text-sm">© {new Date().getFullYear()} Build With Tomisin. All rights reserved.</p>
        </div>

        <div className="flex gap-8 text-sm font-medium text-white/40">
           <Link href="#work" className="hover:text-white transition-colors">Work</Link>
           <Link href="#about" className="hover:text-white transition-colors">About</Link>
           <Link href="#content" className="hover:text-white transition-colors">Legal</Link>
           <Link href="#contact" className="hover:text-white transition-colors">Contact</Link>
        </div>

        <div className="flex items-center gap-2">
           <div className="w-2 h-2 rounded-full bg-green-500 animate-pulse" />
           <span className="text-[10px] uppercase tracking-widest text-white/30">System Status: Optimal</span>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
